import React, { useRef, useEffect } from "react";
import { toast as to, Toaster } from "react-hot-toast";
import DOMPurify from "dompurify";
import { WS_TYPE } from "shared";
import { useToast } from "../contexts/ToastContext";
import { ReadyState } from "react-use-websocket";

//    

export default function Toast() {
  //    
  const { toast, readyState, page } = useToast();
  //     ,   (?)
  const ref = useRef(null);

  const reconnect = () =>
    to(
      <span>
        <b>  ws</b>
        <br />
        ..
      </span>,
      {
        icon: "",
        style: {
          backgroundColor: "rgb(255, 153, 0, 0.4)",
          textAlign: "center",
        },
      }
    );

  const connect = () =>
    to.success(
      <span>
        <b>  ws</b>
        <br />
        !
      </span>,
      {
        style: {
          backgroundColor: "rgb(97, 211, 69, 0.4)",
          textAlign: "center",
        },
      }
    );

  const end = () =>
    to.success(
      <span>
        <b> </b>
        <br />
        {toast !== null &&
        toast !== undefined &&
        toast.hasOwnProperty("message") ? (
          <span
            dangerouslySetInnerHTML={{
              __html: DOMPurify.sanitize(toast.message),
            }}
          ></span>
        ) : (
          ""
        )}
      </span>,
      {
        style: {
          backgroundColor: "rgb(97, 211, 69, 0.4)",
          textAlign: "center",
        },
      }
    );

  const info = () =>
    to(
      <span>
        <b>{toast.head}</b>
        <br />
        {toast !== null &&
        toast !== undefined &&
        toast.hasOwnProperty("message") ? (
          <span
            dangerouslySetInnerHTML={{
              __html: DOMPurify.sanitize(
                toast.head === " "
                  ? String(toast.message)
                      .split("<br />")
                      .map((e) => e.slice(0, 7))
                      .join("<br />")
                  : toast.message
              ),
            }}
          ></span>
        ) : (
          ""
        )}
      </span>,
      {
        icon: "",
        style: {
          backgroundColor: "rgb(158, 223, 255, 0.4)",
          textAlign: "center",
        },
      }
    );

  const vote = () =>
    to(
      <span>
        <b></b>
        <br />
        {String(toast.address).slice(0, 7)}..
      </span>,
      {
        icon: "",
        style: {
          backgroundColor: "rgb(158, 223, 255, 0.4)",
          textAlign: "center",
        },
      }
    );

  const subscribe = () =>
    to(
      <span>
        <b></b>
        <br />
        {String(toast.address).slice(0, 7)}..
      </span>,
      {
        icon: "",
        style: {
          backgroundColor: "rgb(158, 223, 255, 0.4)",
          textAlign: "center",
        },
      }
    );

  const error = () =>
    to.error(
      <span>
        <b>
          
          {toast !== null &&
          toast !== undefined &&
          toast.hasOwnProperty("space")
            ? ` | ${toast.space}`
            : ""}
        </b>
        <br />
        {toast !== null &&
        toast !== undefined &&
        toast.hasOwnProperty("message") ? (
          <span
            dangerouslySetInnerHTML={{
              __html: DOMPurify.sanitize(toast.message),
            }}
          ></span>
        ) : (
          ""
        )}
        ..
      </span>,
      {
        style: {
          backgroundColor: "rgb(255, 0, 0, 0.4)",
          textAlign: "center",
        },
      }
    );

  const error_custom = (custom) =>
    to.error(
      <span>
        <b> | {custom.space}</b>
        <br />
        {custom.message}..
      </span>,
      {
        style: {
          backgroundColor: "rgb(255, 0, 0, 0.4)",
          textAlign: "center",
        },
      }
    );

  const error_end = () =>
    to.error(
      <span>
        <b> </b>
        <br />
        {toast.head}
        <br />
        {toast !== null &&
        toast !== undefined &&
        toast.hasOwnProperty("message") ? (
          <span
            dangerouslySetInnerHTML={{
              __html: DOMPurify.sanitize(toast.message),
            }}
          ></span>
        ) : (
          ""
        )}
      </span>,
      {
        style: {
          backgroundColor: "rgb(255, 0, 0, 0.4)",
          textAlign: "center",
        },
      }
    );

  useEffect(() => {
    if (toast !== null && toast !== ref.current) {
      ref.current = toast;
      //      
      if (page === 1) {
        return;
      }
      switch (toast.type) {
        case WS_TYPE.VOTE:
          vote();
          break;
        case WS_TYPE.SUBSCRIBE:
          subscribe();
          break;
        case WS_TYPE.INFO:
          info();
          break;
        case WS_TYPE.END:
          end();
          break;
        case WS_TYPE.ERROR:
          error();
          break;
        case WS_TYPE.ERROR_END:
          error_end();
          break;
        default:
          error_custom({ space: "  ws", message: " " });
      }
    }
  });

  useEffect(() => {
    switch (readyState) {
      case ReadyState.CLOSING:
        reconnect();
        break;
      case ReadyState.OPEN:
        connect();
        break;
      default:
    }
  }, [readyState]);

  return (
    <>
      <div>
        <Toaster position="top-right" reverseOrder={false} />
      </div>
    </>
  );
}
